<template>
  <div class="registration">
    <Navbar />
    <b-container>
      <h1 class="text-center mt-5">
        Регистрация
      </h1>
      <b-form @submit="registerUser">
        <b-form-group label="Ваш номер телефона:">
          <b-form-input
            v-model="username"
            class="form-control"
            required
          />
        </b-form-group>
        <b-form-group label="Ваше имя:">
          <b-form-input
            v-model="name"
            type="text"
            class="form-control"
            required
          />
        </b-form-group>
        <b-form-group label="Ваша фамилия:">
          <b-form-input
            v-model="last_name"
            type="text"
            class="form-control"
            required
          />
        </b-form-group>
        <b-form-group label="Эл.почта:">
          <b-form-input
            v-model="email"
            aria-required="true"
            class="form-control"
            type="email"
            required
            pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"
          />
        </b-form-group>
        <b-form-group label="Пароль:">
          <b-form-input
            id="text-password"
            v-model="password"
            :state="pasState"
            class="form-control"
            autocomplete="new-password"
            type="password"
            aria-describedby="password-help-block"
            required
          />
          <b-form-text id="password-help-block">
            Ваш пароль должен быть длиной 8-20 символов, содержать буквы и цифры
            и не должен содержит пробелы, специальные символы или эмодзи.
          </b-form-text>
        </b-form-group>

        <b-form-group class="text-right mt-3">
          <b-button
            type="submit"
            size="lg"
            variant="outline-primary"
          >
            Зарегистрироваться
          </b-button>
          <b-form-text>
            Регистрируясь, вы соглашаетесь с
            <br>
            <div>
              <b-link v-b-modal.modal-xl.modal-scrollable>
                пользовательским соглашением
              </b-link>

              <b-modal
                id="modal-scrollable"
                scrollable
                title="Scrollable Content"
              >
                <p
                  v-for="i in 20"
                  :key="i"
                  class="my-4"
                >
                  Cras mattis consectetur purus sit amet fermentum. Cras justo
                  odio, dapibus ac facilisis in, egestas eget quam. Morbi leo
                  risus, porta ac consectetur ac, vestibulum at eros.
                </p>
              </b-modal>
            </div>
          </b-form-text>
        </b-form-group>
      </b-form>
    </b-container>
    <Footer class="ft" />
  </div>
</template>

<script>
import axios from "axios";
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import router from "../router/index";
export default {
    name: "Reg",
    components: {
        Navbar,
        Footer
    },
    props: {},
    data() {
        return {
            username: "",
            name: "",
            last_name: "",
            email: "",
            password: ""
        };
    },
    computed: {
        pasState() {
            return this.password.length > 7 ? true : false;
        }
    },
    methods: {
        registerUser(e) {
            e.preventDefault();
            axios.post("http://bohmann.shop/api/users/", {
                username: this.username,
                name: this.name,
                last_name: this.last_name,
                password: this.password,
                email: this.email
            });
            router.push({ name: "home" });
        }
    }
};
</script>

